
.glowing {
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-name: disruptive-glow;
          animation-name: disruptive-glow;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}
@-webkit-keyframes disruptive-glow {
from {
    background-color: #f33535;
    -webkit-box-shadow: 0 0 9px #f33535;
            box-shadow: 0 0 9px #f33535;
}
50% {
    background-color: #e70e0e;
    -webkit-box-shadow: 0 0 18px #e70e0e;
            box-shadow: 0 0 18px #e70e0e;
}
to {
    background-color: #f33535;
    -webkit-box-shadow: 0 0 9px #f33535;
            box-shadow: 0 0 9px #f33535;
}
}
@keyframes disruptive-glow {
from {
    background-color: #f33535;
    -webkit-box-shadow: 0 0 9px #f33535;
            box-shadow: 0 0 9px #f33535;
}
50% {
    background-color: #e70e0e;
    -webkit-box-shadow: 0 0 18px #e70e0e;
            box-shadow: 0 0 18px #e70e0e;
}
to {
    background-color: #f33535;
    -webkit-box-shadow: 0 0 9px #f33535;
            box-shadow: 0 0 9px #f33535;
}
}
.is-horizontal-center {
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.is-loader {
  -webkit-animation: spin 1s linear infinite;
          animation: spin 1s linear infinite;
}
@-webkit-keyframes spin {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes spin {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}



